import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { ListPage } from '../pages/list/list';
import { ProductPage } from '../pages/product/product';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { ProductDetailPage } from "../pages/product-detail/product-detail";
import { CartService } from "../providers/cart.service";
import { CartPage } from "../pages/cart/cart";
import { ActionSheetPage } from "../pages/action-sheet/action-sheet";
import { AlertPage } from "../pages/alert/alert";
import { BadgePage } from "../pages/badge/badge";
import { ButtonPage } from "../pages/button/button";

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    ListPage,
    ProductPage,
    ProductDetailPage,
    CartPage,
    ActionSheetPage,
    AlertPage,
    BadgePage,
    ButtonPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/product-detail/product-detail.module#ProductDetailPageModule', name: 'ProductDetailPage', segment: 'product-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/product/product.module#ProductPageModule', name: 'ProductPage', segment: 'product', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cart/cart.module#CartPageModule', name: 'CartPage', segment: 'cart', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/action-sheet/action-sheet.module#ActionSheetPageModule', name: 'ActionSheetPage', segment: 'action-sheet', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/alert/alert.module#AlertPageModule', name: 'AlertPage', segment: 'alert', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/badge/badge.module#BadgePageModule', name: 'BadgePage', segment: 'badge', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/button/button.module#ButtonPageModule', name: 'ButtonPage', segment: 'button', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    ListPage,
    ProductPage,
    ProductDetailPage,
    CartPage,
    ActionSheetPage,
    AlertPage,
    BadgePage,
    ButtonPage
  ],
  providers: [
    CartService,
    { provide: "apiUrl", useValue: "http://northwindapi.azurewebsites.net/api" },
    StatusBar,
    SplashScreen,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule { }



